*
    padding: 0
    margin: 0

.main
    position: relative
    min-height: 952px
    padding: 27px 0 80px 0
    background-color:#08080B
    .wrapper
        display: flex
        justify-content: space-between
        width: 921px
        margin-left: 100px
    &_arbuz
        width: 65px
        height: 71px
        display: block
    &_social
        display: flex
        justify-content: space-between
        width: 536px
        margin-left: 392px
        margin-top: 34px
        &_links
            display: flex
            justify-content: space-between
            align-items: center
            width: 192px
            &_img
                display: block
                text-decoration: none
                img
                    display: block
                    width: 23px
                    height: 23px
            &_text
                color: #fff
                font-family: Montserrat
                font-size: 12px
                font-weight: 400
                text-decoration: none
    &_main
        position: relative
        margin: 0 auto
        margin-top: 25px
        width: 1110px
        height: 749px
        &_img
            width: 100%
            height: 100%
            display: block
            border-radius: 4px
            box-shadow: inset 0px 0px 2px 0px rgba(0, 0, 0, 0.08),inset 0px 0px 0px 1px rgba(0, 0, 0, 0.2)
            filter: blur(4px)
            z-index: -1
        &_title
            position: absolute
            top: 0
            right: -145px
            width: 690px
            height: 733px
            background-color: #000
            h1
                font-family: Raleway
                font-size: 96px
                font-weight: 300
                line-height: 144px
                text-align: right
                color: #786D19
                padding-right: 15px
            h2
                text-transform: uppercase
                margin-top: 300px
                font-family: 'Press Start 2P'
                font-size: 40px
                font-weight: 400
                text-align: right
                color:#DCC416

.descr
    min-height: 1088px
    background-color: #000
    padding: 101px 0 25px 0
    &_block
        position: relative
        padding: 563px 35px 135px 35px
        &_y
            position: absolute
            top: -62px
            left: 60px
            width: 1140px
            height: 138px
            background-color: #DCC416
        img
            position: absolute
            top: 30px
            left: 35px
            width: 1137px
            height: 621px
        &_text
            position: relative
            font-family: Montserrat
            font-size: 32px
            font-weight: 400
            line-height: 45.76px
            color: #fff
            z-index: 4
            span
                font-family: 'Press Start 2P'
                font-size: 48px
                font-weight: 400
                color: #DCC416

.composition
    min-height: 923px
    padding: 72px 0 51px 0
    background-color: #08080B
    h1
        font-family: 'Press Start 2P'
        font-size: 64px
        font-weight: 400
        text-align: center
        color: #DCC416
    &_konkretno
        display: flex
        justify-content: space-between
        margin: 0 auto
        margin-top: 82px
        width: 1439px
        height: 449px
        &_descr
            width: 376px
            height: 425px
            border-radius: 0px 0px 10px 10px
            background: rgb(232, 234, 158)
            &:nth-child(2)
                height: 449px
            img
               width: 376px 
               height: 248px
            div
                margin: 50px auto
                width: 288px
                height: 71px
            h2
                color: #2E363E
                font-family: Montserrat
                line-height: 34.32px
                font-size: 24px
                font-weight: 700
                text-align: left
            h3
                color: #2E363E
                font-family: Montserrat
                margin-top: 12px
                font-size: 16px
                font-weight: 400
                line-height: 24px
                text-align: left
    &_quotation
        width: 1344px
        height: 209px
        margin: 0 auto
        margin-top: 20px
        font-family: 'Poiret One'
        font-size: 36px
        font-weight: 400
        line-height: 51.48px
        text-align: right
        color: #fff