@media (max-width: 1400px)
    .main
        min-height: 630px
        &_arbuz
            margin-left: 100px
        .wrapper
            width: 700px
            margin-left: 0
        &_social
            width: 425px
            margin-left: 0
        &_main
            width: 100%
            height: auto
            &_img
                display: block
                margin-left: 100px
                height: 50%
                width: 50%
            &_title
                right: 100px
                width: 450px
                height: 430px
                h1
                    font-size: 70px
                    line-height: 100px
                h2
                    margin-top: 140px
                    font-size: 30px
    .descr
        &_block
            &_y
                top: 100px
                width: 800px
                height: 80px
            img
                width: 800px
                height: auto
                top: 140px
            &_text
                font-size: 22px
                span
                    font-size: 34px
    .composition
        height: auto
        h1
            font-size: 50px
        &_konkretno
            box-sizing: border-box
            width: 100%
            padding: 0 50px
            height: auto
            justify-content: space-around
            &_descr
                width: 260px
                height: 332px
                img
                    width: 260px
                    height: auto
                div
                    margin: 42px 0 42px 10px
                &:nth-child(2)
                    height: 368px
                    div
                        margin: 42px 0 70px 10px
        &_quotation
            width: 100%
            font-size: 24px
            padding-right: 90px

@media (max-width: 1200px)
    .composition
        height: auto
        h1
            font-size: 50px
        &_konkretno
            box-sizing: border-box
            width: 100%
            padding: 0 50px
            height: auto
        &_quotation
            width: 100%
            font-size: 24px
            padding-right: 50px